import api from '~/services/api';
export default {
    name:'Card',
    props: ['url'],
    data(){
        return{
            detail: []
        }
    },
    async created(){
        await this.getPokemonId();
        this.changeImage(this.detail.id,this.detail);
        
    },
    computed:{
        showSrc(){
            try {
                return this.detail;    
            } catch (error) {
                console.error(error)
            }
            
        }
    },
    methods:{
        async getPokemonId(){
            await api.getPokemonId(this.url)
                .then(res => {
                    this.detail = res;
                    
                })
        },
        changeImage(id,detail){
            try {
                let div = document.getElementById(`${id}`)
                //console.log(div);
                let type = detail.types[0].type.name;
                //console.log(detail.types[0].type.name);
                div.style.background = 'url('+`/img/${type}.png`+')';
            } catch (error) {
                console.error(error)
            }
            
        }
    },
    watch:{
        async url(){
            await this.getPokemonId();
            this.changeImage(this.detail.id,this.detail);
        }
    }
}
